import React from "react";
import styled from "styled-components";
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import LocalPhoneIcon from '@mui/icons-material/LocalPhone';
import EmailIcon from '@mui/icons-material/Email';
import { mobile } from "../responsive";

const Container=styled.div`
display:flex;
${mobile({flexDirection:"column"})}
`
const Left=styled.div`
flex:1;
display:flex;
flex-direction:column;
padding:20px;
`
const Logo=styled.h1`
`
const Desc=styled.p`
margin:20px 0px;
`
const SocialContainer=styled.div`
display:flex;

`
const SocialIcons=styled.div`
width:40px;
height:40px;
border-radius:50%;
color:white;
background-color:#${props=>props.color};
display:flex;
align-items:center;
justify-content:center;
margin-left:20px;
`

const Center=styled.div`
flex:1;
padding:20px;
${mobile({display:"none"})}
`

const Title=styled.h3`
margin-bottom:30px;
`
const List=styled.ul`
margin:0;
padding:0;
list-style:none;
display:flex;
flex-wrap:wrap;
`
const ListItem=styled.li`
width:50%;

`
const Right=styled.div`
flex:1;
padding:20px
${mobile({backgroundColor:"#"})}
`
const ContactItem=styled.div`
margin-bottom:20px;
display:flex;
align-items:center;
`
const Payment=styled.img`
width:50%;
`

function Footer(){
    return(
        <Container>
            <Left>
                <Logo>SHOPOLO</Logo>
                <Desc>Lorem ipsum dolor sit amet consectetur adipisicing elit. Inventore numquam ex iste mollitia earum odio at quam magnam, tempora ea!</Desc>
                <SocialContainer>
                    <SocialIcons color="385999">
                        <FacebookIcon/>
                    </SocialIcons>
                    <SocialIcons color="E4405F">
                        <InstagramIcon/>
                    </SocialIcons>
                    <SocialIcons color="55ACEE">
                        <TwitterIcon/>
                    </SocialIcons>
                </SocialContainer>

            </Left>
            <Center>
                <Title>
                    Useful Links
                </Title>
                <List>
                    <ListItem>Home</ListItem>
                    <ListItem>Cart</ListItem>
                    <ListItem>Man Fashion</ListItem>
                    <ListItem>Women Fashion</ListItem>
                    <ListItem>Accesories</ListItem>
                    <ListItem>My Account</ListItem>
                    <ListItem>Order Tracking</ListItem>
                    <ListItem>Wishlist</ListItem>
                    <ListItem>Terms</ListItem>
                </List>
            </Center>
            <Right>
                <Title>
                    Contact
                </Title>
                <ContactItem>
                    <LocationOnIcon style={{marginRight:"10px"}}/>
                    Address Lorem ipsum, dolor sit amet consectetur adipisicing elit.
                </ContactItem>
                <ContactItem>
                    <LocalPhoneIcon style={{marginRight:"10px"}}/>
                    99999999
                </ContactItem>
                <ContactItem>
                    <EmailIcon style={{marginRight:"10px"}}/>
                   a@gmail.com
                </ContactItem>
                <Payment src="data:image/jpeg;base64,"/>
            </Right>
        </Container>
    )
}

export default Footer;